const minute = 60 // 160
const perGu = 100 // 100
const benshu = 10000 // 
export default {
  handleTime(time) { // (2012-12-25 20:11:11)
    // Time = (new Date("2012/12/25 20:11:11")).getTime(); //
    // 
    time = time.replace(new RegExp('-', 'gm'), '/')
    let starttimeMiao = (new Date(time)).getTime() / 1000
    return starttimeMiao
  },
  handleTimeFull(etime) {  // 
    let now = new Date()
    let ytime = now.getFullYear() + '-'
    let mtime = ((now.getMonth() + 1) < 10 ? '0' : '') + (now.getMonth() + 1) + '-'
    let dtime = (now.getDate() < 10 ? '0' : '') + now.getDate()
    etime = ytime + mtime + dtime + ' ' + etime
    return etime
  },
  handleTimeZeroM(time) { // (2012-12-25 20:11:11 == 2012-12-25 20:11:00)
    time = time.substring(0, time.length - 2) + '00'
    return time
  },
  jqMiaoByTime(time) { //  (- 12:12:2020)
    time = time.substring(time.length - 2)
    return time
  },
  jqMinuteByTime(time) { //  - 2012-12-25 20:11:11
    time = time.substring(time.length - 5, time.length - 3)
    return time
  },
  handleNum(start, end) { // 2()
    let num = end - start + 1
    let snum = Math.ceil(num / minute)
    return snum
  },
  handleMinute (num) { // 
    return num * minute
  },
  jsMoney (money, num) { // 
    let sun = (num * perGu * money).toFixed(2)
    return sun
  },
  chengMoney (money, num) { //  -10000
    let sun = money * benshu * perGu * num
    return sun
  },
  chuMoney (money) {  //  -10000
    let sun = (money / benshu).toFixed(2)
    return sun
  }
}



